// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------

body {
  margin: 0;
}

#form {
  background: rgba(0, 0, 0, 0.15);
  padding: 0.25rem;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  height: 3rem;
  box-sizing: border-box;
  backdrop-filter: blur(10px);

  & > button {
    background: #333;
    border: none;
    padding: 0 1rem;
    margin: 0.25rem;
    border-radius: 3px;
    outline: none;
    color: #fff;
    width: 80px;
  }
}

#input {
  border: none;
  padding: 0 1rem;
  flex-grow: 1;
  border-radius: 2rem;
  margin: 0.25rem;
  width: calc(100% - 80px);

  &:focus {
    outline: none;
  }
}

#messages {
  list-style-type: none;
  margin: 0;
  padding: 0;

  & > li {
    padding: 0.5rem 1rem;
    font-size: 16px;

    span {
      font-weight: bold;
      background-color: #696edc;
      color: white;
      border-radius: 2px;
      padding: 3px 15px;
      margin-right: 5px;
      font-size: 14px;
    }

    &:nth-child(odd) {
      background: #eae5f7;
    }
  }
}
